// material
import {
  Divider,
  FormControl,
  InputLabel,
  MenuItem,
  Select,
  Stack,
  TextField,
} from '@mui/material';
import { Form, FormikProvider, useFormik } from 'formik';
import { useNavigate } from 'react-router-dom';
import * as Yup from 'yup';

// ----------------------------------------------------------------------

export default function FaultDetailForm(props) {
  const { fault = {} } = props;
  const navigate = useNavigate();

  const LoginSchema = Yup.object().shape({
    email: Yup.string()
      .email('Email must be a valid email address')
      .required('Email is required'),
    password: Yup.string().required('Password is required'),
  });

  const formik = useFormik({
    initialValues: fault,
    validationSchema: LoginSchema,
    onSubmit: () => {
      navigate('/dashboard', { replace: true });
    },
  });

  const { errors, touched, values, isSubmitting, handleSubmit, getFieldProps } =
    formik;

  return (
    <FormikProvider value={formik}>
      <Form autoComplete="off" noValidate onSubmit={handleSubmit}>
        <Stack spacing={3} mt={2} mb={2}>
          <Divider></Divider>
          <TextField fullWidth label="" {...getFieldProps('pic.name')} />
          <TextField fullWidth label="" {...getFieldProps('lineNo')} />

          <TextField
            fullWidth
            label=""
            {...getFieldProps('lineName')}
          />

          <TextField fullWidth label="" {...getFieldProps('company')} />

          <TextField
            fullWidth
            label=""
            {...getFieldProps('voltageGrade')}
          />

          <TextField
            fullWidth
            label=""
            {...getFieldProps('faultDevice')}
          />

          <TextField
            fullWidth
            label=""
            {...getFieldProps('faultPart')}
          />

          <FormControl fullWidth>
            <InputLabel></InputLabel>
            <Select {...getFieldProps('faultReason')}>
              <MenuItem value={10}></MenuItem>
              <MenuItem value={20}></MenuItem>
              <MenuItem value={30}></MenuItem>
              <MenuItem value={40}></MenuItem>
              <MenuItem value={50}></MenuItem>
            </Select>
          </FormControl>

          <TextField
            fullWidth
            multiline
            label=""
            {...getFieldProps('faultReasonMark')}
          />

          <TextField
            fullWidth
            label=""
            {...getFieldProps('faultReason')}
          />

          <TextField
            fullWidth
            label=""
            {...getFieldProps('faultReason')}
          />

          <Divider></Divider>
          <TextField fullWidth label="" />
        </Stack>
      </Form>
    </FormikProvider>
  );
}
