import closeFill from '@iconify/icons-eva/close-fill';
import roundClearAll from '@iconify/icons-ic/round-clear-all';
import roundFilterList from '@iconify/icons-ic/round-filter-list';
import { Icon } from '@iconify/react';
import { DateTimePicker } from '@mui/lab';
// material
import {
  Box,
  Button,
  Checkbox,
  Collapse,
  Divider,
  Drawer,
  FormControlLabel,
  FormGroup,
  IconButton,
  Stack,
  Switch,
  TextField,
  Typography,
} from '@mui/material';
import { Form, FormikProvider } from 'formik';
import PropTypes from 'prop-types';
import { useState } from 'react';
//
import Scrollbar from '../../Scrollbar';

// ----------------------------------------------------------------------

export const SORT_BY_OPTIONS = [
  { value: 'featured', label: 'Featured' },
  { value: 'newest', label: 'Newest' },
  { value: 'priceDesc', label: 'Price: High-Low' },
  { value: 'priceAsc', label: 'Price: Low-High' },
];
export const FILTER_GENDER_OPTIONS = [
  '',
  '',
  '',
  '',
  '',
];
export const FILTER_CATEGORY_OPTIONS = [
  '',
  '',
  '',
  '',
  '',
];
export const FILTER_RATING_OPTIONS = [
  'up4Star',
  'up3Star',
  'up2Star',
  'up1Star',
];
export const FILTER_PRICE_OPTIONS = [
  { value: 'below', label: 'Below $25' },
  { value: 'between', label: 'Between $25 - $75' },
  { value: 'above', label: 'Above $75' },
];
export const FILTER_COLOR_OPTIONS = ['', '', ''];

// ----------------------------------------------------------------------

ShopFilterSidebar.propTypes = {
  isOpenFilter: PropTypes.bool,
  onResetFilter: PropTypes.func,
  onOpenFilter: PropTypes.func,
  onCloseFilter: PropTypes.func,
  formik: PropTypes.object,
};

export default function ShopFilterSidebar({
  isOpenFilter,
  onResetFilter,
  onOpenFilter,
  onCloseFilter,
  formik,
}) {
  const { values, getFieldProps } = formik;
  const [switches, setSwitches] = useState({
    gender: false,
    category: false,
    colors: false,
    timeRange: false,
  });

  console.log(getFieldProps('startTime'));

  const handleSwitch = key => (o, hide) => {
    setSwitches({
      ...switches,
      [key]: hide,
    });
  };

  return (
    <>
      <Button
        disableRipple
        color="inherit"
        endIcon={<Icon icon={roundFilterList} />}
        onClick={onOpenFilter}
      >
        &nbsp;
      </Button>

      <FormikProvider value={formik}>
        <Form autoComplete="off" noValidate>
          <Drawer
            anchor="right"
            open={isOpenFilter}
            onClose={onCloseFilter}
            PaperProps={{
              sx: { width: 280, border: 'none', overflow: 'hidden' },
            }}
          >
            <Stack
              direction="row"
              alignItems="center"
              justifyContent="space-between"
              sx={{ px: 1, py: 2 }}
            >
              <Typography variant="subtitle1" sx={{ ml: 1 }}>
                
              </Typography>
              <IconButton onClick={onCloseFilter}>
                <Icon icon={closeFill} width={20} height={20} />
              </IconButton>
            </Stack>

            <Divider />

            <Scrollbar>
              <Stack spacing={3} sx={{ p: 3 }}>
                <div>
                  <Typography variant="subtitle1" gutterBottom>
                    
                    <Switch onChange={handleSwitch('gender')} />
                  </Typography>
                  <FormGroup>
                    <Collapse in={switches.gender}>
                      {FILTER_GENDER_OPTIONS.map(item => (
                        <FormControlLabel
                          key={item}
                          control={
                            <Checkbox
                              {...getFieldProps('gender')}
                              value={item}
                              checked={values.gender.includes(item)}
                            />
                          }
                          label={item}
                        />
                      ))}
                    </Collapse>
                  </FormGroup>
                </div>

                <div>
                  <Typography variant="subtitle1" gutterBottom>
                    
                    <Switch onChange={handleSwitch('category')} />
                  </Typography>
                  <FormGroup>
                    <Collapse in={switches.category}>
                      {FILTER_CATEGORY_OPTIONS.map(item => (
                        <FormControlLabel
                          key={item}
                          control={
                            <Checkbox
                              {...getFieldProps('category')}
                              value={item}
                              checked={values.category.includes(item)}
                            />
                          }
                          label={item}
                        />
                      ))}
                    </Collapse>
                  </FormGroup>
                </div>

                <div>
                  <Typography variant="subtitle1" gutterBottom>
                    
                    <Switch onChange={handleSwitch('colors')} />
                  </Typography>
                  <Collapse in={switches.colors}>
                    {FILTER_COLOR_OPTIONS.map(item => (
                      <FormControlLabel
                        key={item}
                        control={
                          <Checkbox
                            {...getFieldProps('colors')}
                            value={item}
                            checked={values.colors.includes(item)}
                          />
                        }
                        label={item}
                      />
                    ))}
                  </Collapse>
                </div>

                <div>
                  <Typography variant="subtitle1" gutterBottom>
                    
                    <Switch onChange={handleSwitch('timeRange')} />
                  </Typography>
                  <FormGroup>
                    <Collapse in={switches.timeRange}>
                      <Stack spacing={3}>
                        <div></div>
                        <DateTimePicker
                          renderInput={props => <TextField {...props} />}
                          label=""
                          {...getFieldProps('startTime')}
                        />
                        <DateTimePicker
                          renderInput={props => <TextField {...props} />}
                          label=""
                          {...getFieldProps('endTime')}
                        />
                      </Stack>
                    </Collapse>
                  </FormGroup>
                </div>
              </Stack>
            </Scrollbar>

            <Box sx={{ p: 3 }}>
              <Button
                fullWidth
                size="large"
                type="submit"
                color="inherit"
                variant="outlined"
                onClick={onResetFilter}
                startIcon={<Icon icon={roundClearAll} />}
              >
                Clear All
              </Button>
            </Box>
          </Drawer>
        </Form>
      </FormikProvider>
    </>
  );
}
