import faker from 'faker';
import { sample } from 'lodash';
import { mock, Random } from 'mockjs';

// ----------------------------------------------------------------------

window.Random = Random;
const users = [...Array(24)].map((_, index) => ({
  id: faker.datatype.uuid(),
  // 
  company: '',
  // 
  time: mock('@datetime'),
  // 
  lineNo: Random.natural(100000, 90000),
  // 
  lineName: ': ' + mock('@word'),

  // 
  voltageGrade: Random.natural(1, 4),

  // 
  faultDevice: ': ' + mock('@word'),

  faultPart: ': ' + mock('@word'),

  // 
  faultReason: sample(['', '', '', '', '']),

  faultReasonMark: mock('@csentence(10, 20)'),

  cost: mock('@sentence'),

  // person in charge
  pic: {
    name: mock('@cname'),
    avatar: `/static/mock-images/avatars/avatar_${Random.natural(1, 24)}.jpg`,
  },
  status: Random.pick(['', '', '']),

  cover: `/static/mock-images/covers/cover_${Random.natural(1, 24)}.jpg`,
}));

export default users;
